.header {
    background-image: url(../../../build/img/desktop/image-hero.webp);
    height: 60rem;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    position: relative;
}

.header-barra {
    //display: flex;
    //justify-content: space-between;
    padding-top: 4rem;
    & :after {
        content: '';
        clear: both;
        display: block;
    }
}

//Menu Hamburguesa
.navegacion {
    top: 6.5rem;
    right: 0;
    position: absolute;
    background-color: $negro;
    z-index: 1;
    clip-path: circle(0px at top right);
    transition: clip-path ease-in-out 700ms;
    & ul {
        margin: 0;
        padding: 0;
        list-style: none;
    }
    & .navegacion-enlace {
        color: $blanco;
        display: block;
        padding: 4rem 9rem;
        text-transform: uppercase;
        font-size: 1.8rem;
    }
}

.site-nav-open {
    clip-path: circle(150% at top right);
}

.site-nav--icon {
    display: inline-block;
    font-size: 1.5em;
    margin-right: 1em;
    width: 1.1em;
    text-align: right;
}

.menu-toggle {
    padding: 2.5rem;
    position: absolute;
    top: 2rem;
    right: 2rem;
}

.menu-open .hamburguer {
    transform: rotate(45deg);
}

.menu-open .hamburguer::before {
    opacity: 0;
}

.menu-open .hamburguer::after {
    transform: translateY(-3px) rotate(-90deg);
}

.hamburguer,
.hamburguer::before,
.hamburguer::after {
    content: '';
    display: block;
    background: $blanco;
    height: 3px;
    width: 1.75em;
    border-radius: 3px;
    transition: all ease-in-out 500ms;
}

.hamburguer::before {
    transform: translateY(-6px);
}

.hamburguer::after {
    transform: translateY(3px);
}

//Fin menu hamburguesa
.navegacion-enlace {
    color: $blanco;
    text-decoration: none;
    margin-right: 1.5rem;
    &:hover {
        text-decoration-line: underline;
    }
}

.overlay {
    position: absolute;
    top: 35%;
    width: 95%;
    border: 2px solid $blanco;
    & p {
        color: $blanco;
        font-size: 4.5rem;
        margin: 0 0;
        padding: 2rem;
        text-transform: uppercase;
        font-family: $fuenteSecundaria;
    }
}

@include tablet {
    .overlay {
        width: 70%;
        & p {
            font-size: 6rem;
        }
    }
}

@include desktop {
    .overlay {
        width: 50%;
        & p {
            font-size: 6rem;
        }
    }
    //Volver al Menu inicial
    .menu-toggle {
        display: none;
    }
    .navegacion {
        height: auto;
        position: absolute;
        background: transparent;
        float: right;
        margin-right: 1.5rem;
        clip-path: initial;
    }
    .navegacion li {
        display: inline-block;
        border: none;
    }
    .navegacion .navegacion-enlace {
        padding: 0;
        margin-left: 3rem;
        text-transform: initial;
    }
    .site-nav--icon {
        display: none;
    }
    .navegacion a:hover,
    .navegacion a:focus {
        background: transparent;
    }
    .header-logo {
        padding: 2rem;
    }
}