.galeria {
    margin-top: 9rem;
    padding-bottom: 7rem;
    position: relative;
}

.galeria-encabezado {
    margin-bottom: 5rem;
    h3 {
        font-family: $fuenteSecundaria;
        font-size: 4rem;
        text-align: center;
    }
}

.boton {
    position: absolute;
    left: 40%;
    bottom: 0%;
    border: 2px solid $negro;
    text-transform: uppercase;
    text-align: center;
    padding: 0;
    margin: 0;
    font-family: $fuenteSecundaria;
    font-size: .5rem;
    padding: 0 3rem;
    z-index: 1;
    & p {
        margin: 0;
        padding: 1rem;
    }
    &:hover {
        background-color: $negro;
        cursor: pointer;
        p {
            color: $blanco;
        }
    }
}

.image {
    position: relative;
    img {
        margin-bottom: 2rem;
    }
    p {
        position: absolute;
        bottom: 0%;
        color: $blanco;
        text-transform: uppercase;
        font-family: $fuenteSecundaria;
        font-size: 3rem;
        z-index: 1;
    }
}

@include desktop {
    .galery-image {
        @include grid (4, $separacion / 3);
    }
    .galeria-encabezado {
        display: flex;
        justify-content: space-between;
        margin-bottom: 5rem;
    }
    .boton {
        position: initial;
    }
}

@include tablet {
    .galery-image {
        @include grid(4, $separacion / 3);
    }
    .galeria-encabezado {
        display: flex;
        justify-content: space-between;
        margin-bottom: 5rem;
    }
    .image p {
        position: absolute;
        bottom: 0%;
    }
    .boton {
        position: initial;
    }
}