    .footer {
        background-color: $negro;
        padding: 7rem 0;
        margin-top: 8rem;
    }
    
    .footer-general {
        text-align: center;
    }
    
    .footer-logo {
        & img {
            padding-bottom: 3rem;
        }
    }
    
    .footer-nav {
        & ul {
            padding: 0;
        }
        & li {
            list-style: none;
            padding-bottom: $separacion;
        }
        & a {
            color: $blanco;
            font-family: $fuentePrincipal;
        }
    }
    
    .footer-redes {
        & a {
            color: $blanco;
            size: 2rem;
        }
        & .footer-icono {
            width: 4rem;
            font-size: 3rem;
        }
    }
    
    .footer-derechos {
        & p {
            color: $grisFuerte;
        }
    }
    
    @include tablet {
        li {
            display: inline-block;
            margin-right: 3rem;
        }
        .footer {
            padding: 4rem;
            position: relative;
        }
        .footer-general {
            text-align: initial;
        }
        .footer-redes {
            position: absolute;
            right: 15%;
            top: 15%;
        }
        .footer-derechos {
            position: absolute;
            right: 15%;
            bottom: 33%;
        }
    }