// Sintaxis para declara un mixing, los mixing pueden tener parametros 
@mixin telefono {
    @media (min-width: $telefono) {
        // @content nos sirve para hacer el mixing dinamico y poner el codigo CSS en las paginas de Layout
        @content;
    }
}

@mixin tablet {
    @media (min-width: $tablet) {
        // @content nos sirve para hacer el mixing dinamico y poner el codigo CSS en las paginas de Layout
        @content;
    }
}

@mixin desktop {
    @media (min-width: $desktop) {
        // @content nos sirve para hacer el mixing dinamico y poner el codigo CSS en las paginas de Layout
        @content;
    }
}

@mixin grid ($columnas, $espaciado) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}