.about {
    margin-top: 10rem;
}

.text-about {
    padding: 2rem;
    background-color: $blanco;
    font-family: $fuenteSecundaria;
    & h3 {
        text-transform: uppercase;
        font-size: 5rem;
        text-align: center;
    }
    & .contain-about {
        font-size: 1.5rem;
        line-height: 1.5;
        text-align: center;
    }
}

@include desktop {
    .about {
        @include grid(2, 0);
    }
    .text-about {
        position: relative;
        top: 33%;
        margin-left: -10.5rem;
    }
}