html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "Alata", sans-serif;
  font-size: 1.6rem; }

p {
  font-size: 2rem;
  color: #000; }

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1,
h2,
h3 {
  margin: 0; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

.header {
  background-image: url(../../../build/img/desktop/image-hero.webp);
  height: 60rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  position: relative; }

.header-barra {
  padding-top: 4rem; }
  .header-barra :after {
    content: '';
    clear: both;
    display: block; }

.navegacion {
  top: 6.5rem;
  right: 0;
  position: absolute;
  background-color: #000;
  z-index: 1;
  clip-path: circle(0px at top right);
  transition: clip-path ease-in-out 700ms; }
  .navegacion ul {
    margin: 0;
    padding: 0;
    list-style: none; }
  .navegacion .navegacion-enlace {
    color: #FFF;
    display: block;
    padding: 4rem 9rem;
    text-transform: uppercase;
    font-size: 1.8rem; }

.site-nav-open {
  clip-path: circle(150% at top right); }

.site-nav--icon {
  display: inline-block;
  font-size: 1.5em;
  margin-right: 1em;
  width: 1.1em;
  text-align: right; }

.menu-toggle {
  padding: 2.5rem;
  position: absolute;
  top: 2rem;
  right: 2rem; }

.menu-open .hamburguer {
  transform: rotate(45deg); }

.menu-open .hamburguer::before {
  opacity: 0; }

.menu-open .hamburguer::after {
  transform: translateY(-3px) rotate(-90deg); }

.hamburguer,
.hamburguer::before,
.hamburguer::after {
  content: '';
  display: block;
  background: #FFF;
  height: 3px;
  width: 1.75em;
  border-radius: 3px;
  transition: all ease-in-out 500ms; }

.hamburguer::before {
  transform: translateY(-6px); }

.hamburguer::after {
  transform: translateY(3px); }

.navegacion-enlace {
  color: #FFF;
  text-decoration: none;
  margin-right: 1.5rem; }
  .navegacion-enlace:hover {
    text-decoration-line: underline; }

.overlay {
  position: absolute;
  top: 35%;
  width: 95%;
  border: 2px solid #FFF; }
  .overlay p {
    color: #FFF;
    font-size: 4.5rem;
    margin: 0 0;
    padding: 2rem;
    text-transform: uppercase;
    font-family: "Josefin Sans", sans-serif; }

@media (min-width: 768px) {
  .overlay {
    width: 70%; }
    .overlay p {
      font-size: 6rem; } }

@media (min-width: 1200px) {
  .overlay {
    width: 50%; }
    .overlay p {
      font-size: 6rem; }
  .menu-toggle {
    display: none; }
  .navegacion {
    height: auto;
    position: absolute;
    background: transparent;
    float: right;
    margin-right: 1.5rem;
    clip-path: initial; }
  .navegacion li {
    display: inline-block;
    border: none; }
  .navegacion .navegacion-enlace {
    padding: 0;
    margin-left: 3rem;
    text-transform: initial; }
  .site-nav--icon {
    display: none; }
  .navegacion a:hover,
  .navegacion a:focus {
    background: transparent; }
  .header-logo {
    padding: 2rem; } }

.about {
  margin-top: 10rem; }

.text-about {
  padding: 2rem;
  background-color: #FFF;
  font-family: "Josefin Sans", sans-serif; }
  .text-about h3 {
    text-transform: uppercase;
    font-size: 5rem;
    text-align: center; }
  .text-about .contain-about {
    font-size: 1.5rem;
    line-height: 1.5;
    text-align: center; }

@media (min-width: 1200px) {
  .about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0; }
  .text-about {
    position: relative;
    top: 33%;
    margin-left: -10.5rem; } }

.galeria {
  margin-top: 9rem;
  padding-bottom: 7rem;
  position: relative; }

.galeria-encabezado {
  margin-bottom: 5rem; }
  .galeria-encabezado h3 {
    font-family: "Josefin Sans", sans-serif;
    font-size: 4rem;
    text-align: center; }

.boton {
  position: absolute;
  left: 40%;
  bottom: 0%;
  border: 2px solid #000;
  text-transform: uppercase;
  text-align: center;
  padding: 0;
  margin: 0;
  font-family: "Josefin Sans", sans-serif;
  font-size: .5rem;
  padding: 0 3rem;
  z-index: 1; }
  .boton p {
    margin: 0;
    padding: 1rem; }
  .boton:hover {
    background-color: #000;
    cursor: pointer; }
    .boton:hover p {
      color: #FFF; }

.image {
  position: relative; }
  .image img {
    margin-bottom: 2rem; }
  .image p {
    position: absolute;
    bottom: 0%;
    color: #FFF;
    text-transform: uppercase;
    font-family: "Josefin Sans", sans-serif;
    font-size: 3rem;
    z-index: 1; }

@media (min-width: 1200px) {
  .galery-image {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1.66667rem; }
  .galeria-encabezado {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5rem; }
  .boton {
    position: initial; } }

@media (min-width: 768px) {
  .galery-image {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1.66667rem; }
  .galeria-encabezado {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5rem; }
  .image p {
    position: absolute;
    bottom: 0%; }
  .boton {
    position: initial; } }

.footer {
  background-color: #000;
  padding: 7rem 0;
  margin-top: 8rem; }

.footer-general {
  text-align: center; }

.footer-logo img {
  padding-bottom: 3rem; }

.footer-nav ul {
  padding: 0; }

.footer-nav li {
  list-style: none;
  padding-bottom: 5rem; }

.footer-nav a {
  color: #FFF;
  font-family: "Alata", sans-serif; }

.footer-redes a {
  color: #FFF;
  size: 2rem; }

.footer-redes .footer-icono {
  width: 4rem;
  font-size: 3rem; }

.footer-derechos p {
  color: dimgray; }

@media (min-width: 768px) {
  li {
    display: inline-block;
    margin-right: 3rem; }
  .footer {
    padding: 4rem;
    position: relative; }
  .footer-general {
    text-align: initial; }
  .footer-redes {
    position: absolute;
    right: 15%;
    top: 15%; }
  .footer-derechos {
    position: absolute;
    right: 15%;
    bottom: 33%; } }
